.left-arrow-icon{
    position: absolute;
    top: 300px;
    left: 10px;

    border: 1px solid black;
    color: black;
    border-radius: 30px;
}
.right-arrow-icon{
    position: absolute;
    top: 300px;
    right: 10px;

    border: 1px solid black;
    color: black;
    border-radius: 30px;
}